import {
    getAllQuestions,
    searchQuestions,
    getQuestionById,
    createQuestion,
    getCurrentUser,
    answerQuestion,
    deleteQuestion,
} from "../../../Api/CommunityApi/CommunityApi";

import { Navbar } from "../Navbar";
import { LeftSidebar, RightSidebar } from "../Sidebar";

