const ActionButton = styled(Button)`
    margin-right: 10px;
    &:last-child {
        background-color: #00adb5;
        &:hover {
            background-color: #30619c;
        }
    }
`;

export function QAModal() {
    const [questions, setQuestions] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(0);
    const [category, setCategory] = useState("all");
    const [keyword, setKeyword] = useState("");
    const [selectedQuestion, setSelectedQuestion] = useState(null);
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const [isAdmin, setIsAdmin] = useState(false);
    const [showAnswerForm, setShowAnswerForm] = useState(false);
    const [newQuestion, setNewQuestion] = useState({
        title: "",
        content: "",
        category: "01",
    });
    const [currentUser, setCurrentUser] = useState(null);
    const [answerContent, setAnswerContent] = useState("");
    const pageSize = 5;

    const fetchQuestions = async (page = 1, selectedCategory = "all") => {
        try {
            const data = await getAllQuestions(
                page - 1,
                pageSize,
                selectedCategory !== "all" ? selectedCategory : null
            );
            setQuestions(data.content);
            setTotalPages(data.totalPages);
            setCurrentPage(page);
        } catch (error) {
            console.error("Error fetching questions:", error);
        }
    };

    const checkLoginStatus = async () => {
        try {
            const user = await getCurrentUser();
            if (user && user.userId) {
                setIsLoggedIn(true);
                setCurrentUser(user);

                if (user.authority && typeof user.authority === "object") {
                    const isAdminUser =
                        user.authority.authorityName === "ROLE_ADMIN";
                    setIsAdmin(isAdminUser);
                }
            } else {
                setIsLoggedIn(false);
            }
        } catch (error) {
            console.error("Error fetching current user:", error);
        }
    };

    const toggleAnswerForm = () => {
        setShowAnswerForm(!showAnswerForm);
    };

    const handleSearch = async () => {
        try {
            if (keyword.trim()) {
                const data = await searchQuestions(
                    keyword,
                    currentPage - 1,
                    pageSize
                );
                setQuestions(data.content);
                setTotalPages(data.totalPages);
            } else {
                fetchQuestions(currentPage, category);
            }
        } catch (error) {
            console.error("Error searching questions:", error);
        }
    };

    const handleSubmitQuestion = async (e) => {
        e.preventDefault();
        if (!currentUser || !currentUser.userId) {
            alert("   .");
            return;
        }

        try {
            const questionData = {
                lmsQaTitle: newQuestion.title,
                lmsQaContent: newQuestion.content,
                categoryId: newQuestion.category,
                lmsQaWritingDate: new Date().toISOString().split("T")[0],
                user: {
                    userId: currentUser.userId,
                    userNameKor: currentUser.userNameKor,
                },
            };
            await createQuestion(questionData);

            alert(" .");

            setNewQuestion({ title: "", content: "", category: "01" });
            setSelectedQuestion(null);
            fetchQuestions(currentPage, category);
        } catch (error) {
            console.error("Error creating question:", error);
        }
    };

    const handleSubmitAnswer = async () => {
        if (!answerContent.trim()) {
            alert("  .");
            return;
        }

        try {
            await answerQuestion(selectedQuestion.lmsQaSeq, {
                lmsQaAnswerContent: answerContent,
                lmsQaAnswerWriter: currentUser.userNameKor,
                lmsQaAnswerDate: new Date().toISOString().split("T")[0],
            });
            alert(" .");
            setAnswerContent("");
            loadQuestionDetails(selectedQuestion.lmsQaSeq);
        } catch (error) {
            console.error("Error submitting answer:", error);
        }
    };

    const loadQuestionDetails = async (questionId) => {
        try {
            const question = await getQuestionById(questionId);
            setAnswerContent(question.lmsQaAnswerContent || "");
            setSelectedQuestion(question);
        } catch (error) {
            console.error("Error loading question details:", error);
        }
    };

    const goBackToList = () => {
        setSelectedQuestion(null);
        setShowAnswerForm(false);
    };

    const handlePageChange = (newPage) => {
        if (newPage > 0 && newPage <= totalPages) {
            setCurrentPage(newPage);
            fetchQuestions(newPage, category);
        }
    };

    const handleCategoryChange = (newCategory) => {
        setCategory(newCategory);
        setCurrentPage(1);
        fetchQuestions(1, newCategory);
    };

    const handleDeleteQuestion = async () => {
        try {
            await deleteQuestion(selectedQuestion.lmsQaSeq);
            alert(" .");
            goBackToList();
            fetchQuestions(currentPage, category);
        } catch (error) {
            console.error("Error deleting question:", error);
            alert("  .");
        }
    };

    useEffect(() => {
        fetchQuestions(currentPage, category);
        checkLoginStatus();
    }, [category, currentPage]);

    return (
        <>
            {/* <Navbar />
      <LeftSidebar /> */}

            <Container>
                <Header></Header>

                {!selectedQuestion && (
                    <>
                        <ButtonContainer>
                            <CategoryButton
                                active={category === "all"}
                                onClick={() => handleCategoryChange("all")}
                            >
                                
                            </CategoryButton>
                            <CategoryButton
                                active={category === "01"}
                                onClick={() => handleCategoryChange("01")}
                            >
                                
                            </CategoryButton>
                            <CategoryButton
                                active={category === "02"}
                                onClick={() => handleCategoryChange("02")}
                            >
                                
                            </CategoryButton>
                            <CategoryButton
                                active={category === "03"}
                                onClick={() => handleCategoryChange("03")}
                            >
                                
                            </CategoryButton>
                            <CategoryButton
                                active={category === "99"}
                                onClick={() => handleCategoryChange("99")}
                            >
                                
                            </CategoryButton>
                            {isLoggedIn && (
                                <WriteButton
                                    onClick={() => setSelectedQuestion("new")}
                                >
                                     
                                </WriteButton>
                            )}
                        </ButtonContainer>

                        <div style={{ marginTop: "20px" }}>
                            <Input
                                type="text"
                                placeholder=" "
                                value={keyword}
                                onChange={(e) => setKeyword(e.target.value)}
                            />
                            <Button onClick={handleSearch}></Button>
                        </div>

                        <Table>
                            <thead>
                                <tr>
                                    <th>No</th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                {questions.map((question, index) => (
                                    <tr key={question.lmsQaSeq}>
                                        <td>
                                            {index +
                                                1 +
                                                (currentPage - 1) * pageSize}
                                        </td>
                                        <td>
                                            {question.categoryId === "01"
                                                ? ""
                                                : question.categoryId === "02"
                                                ? ""
                                                : question.categoryId === "03"
                                                ? ""
                                                : ""}
                                        </td>
                                        <td
                                            className="title" //    
                                            style={{ cursor: "pointer" }}
                                            onClick={() =>
                                                loadQuestionDetails(
                                                    question.lmsQaSeq
                                                )
                                            }
                                        >
                                            {question.lmsQaTitle}
                                        </td>
                                        <td>
                                            {question.user?.userNameKor ||
                                                "  "}
                                        </td>
                                        <td>{question.lmsQaWritingDate}</td>
                                        <td>
                                            {question.lmsQaAnswerCheck === "Y"
                                                ? ""
                                                : ""}
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </Table>
                        <PaginationContainer>
                            <PaginationButton
                                onClick={() =>
                                    handlePageChange(currentPage - 1)
                                }
                                disabled={currentPage === 1} //    
                            >
                                
                            </PaginationButton>

                            {Array.from({ length: totalPages }, (_, i) => (
                                <PaginationButton
                                    key={i + 1}
                                    onClick={() => handlePageChange(i + 1)} //        
                                    disabled={i + 1 === currentPage} //   
                                >
                                    {i + 1}
                                </PaginationButton>
                            ))}

                            <PaginationButton
                                onClick={() =>
                                    handlePageChange(currentPage + 1)
                                }
                                disabled={currentPage === totalPages} //    
                            >
                                
                            </PaginationButton>
                        </PaginationContainer>
                    </>
                )}

                {selectedQuestion === "new" && (
                    <QuestionForm onSubmit={handleSubmitQuestion}>
                        <h2>  </h2>
                        <div>
                            <label>: </label>
                            <select
                                value={newQuestion.category}
                                onChange={(e) =>
                                    setNewQuestion({
                                        ...newQuestion,
                                        category: e.target.value,
                                    })
                                }
                            >
                                <option value="01"></option>
                                <option value="02"></option>
                                <option value="03"></option>
                                <option value="99"></option>
                            </select>
                        </div>
                        <div>
                            <label>: </label>
                            <input
                                type="text"
                                value={newQuestion.title}
                                onChange={(e) =>
                                    setNewQuestion({
                                        ...newQuestion,
                                        title: e.target.value,
                                    })
                                }
                                required
                                placeholder=" " //    placeholder 
                            />
                        </div>
                        <div>
                            <label>: </label>
                            <textarea
                                value={newQuestion.content}
                                onChange={(e) =>
                                    setNewQuestion({
                                        ...newQuestion,
                                        content: e.target.value,
                                    })
                                }
                                required
                                placeholder=" " //    placeholder 
                            />
                        </div>
                        <button type="submit"> </button>
                        <button type="button" onClick={() => goBackToList()}>
                            
                        </button>
                    </QuestionForm>
                )}

                {selectedQuestion && selectedQuestion !== "new" && (
                    <QuestionDetailContainer>
                        <QuestionTitle>
                            {selectedQuestion.lmsQaTitle}
                        </QuestionTitle>
                        <InfoText>
                            <strong>:</strong>{" "}
                            {selectedQuestion.user?.userNameKor || "  "}
                        </InfoText>
                        <InfoText>
                            <strong>:</strong>{" "}
                            {selectedQuestion.lmsQaWritingDate}
                        </InfoText>
                        <InfoText>
                            <strong>:</strong>{" "}
                            {selectedQuestion.lmsQaContent}
                        </InfoText>
                        <InfoText>
                            <strong> :</strong>{" "}
                            {selectedQuestion.lmsQaAnswerCheck === "Y"
                                ? ""
                                : ""}
                        </InfoText>

                        {selectedQuestion.lmsQaAnswerContent && (
                            <AnswerSection>
                                <h4> </h4>
                                <p>{selectedQuestion.lmsQaAnswerContent}</p>
                            </AnswerSection>
                        )}

                        {isAdmin && (
                            <AdminSection>
                                <h3>
                                    {" "}
                                    {selectedQuestion.lmsQaAnswerContent
                                        ? ""
                                        : ""}
                                </h3>
                                <textarea
                                    value={answerContent}
                                    onChange={(e) =>
                                        setAnswerContent(e.target.value)
                                    }
                                    placeholder="  ."
                                    style={{ height: "200px" }}
                                />
                                <ActionButton onClick={handleSubmitAnswer}>
                                    {selectedQuestion.lmsQaAnswerContent
                                        ? " "
                                        : " "}
                                </ActionButton>
                            </AdminSection>
                        )}

                        <ActionButton onClick={goBackToList}>
                             
                        </ActionButton>

                        {isAdmin && (
                            <ActionButton onClick={handleDeleteQuestion}>
                                 
                            </ActionButton>
                        )}
                    </QuestionDetailContainer>
                )}
            </Container>
        </>
    );
}

export default QAModal;
