import { useParams } from "react-router-dom";
import { getContentsByLectureId } from "../../../Api/LeaningApi/LeaningApi";
import { useEffect, useState } from "react";
import { getTeacherByLectureId } from "../../../Api/TeacherApi/TeacherApi";
import {
    getLecture,
    getLectureProgressDetails,
} from "../../../Api/LectureApi/LectureApi";
import styled, { keyframes } from "styled-components";
import { LeftSidebar } from "../Sidebar";
import { Navbar } from "../Navbar";

const fadeIn = keyframes`
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
`;

const Container = styled.div`
    box-sizing: border-box;
    padding: 176px 50px 0 240px;
    transition: all 0.3s;
    display: grid;
    grid-template-columns: 70% 30%;
    animation: ${fadeIn} 0.6s ease-out;
    width: 100%;
`;
const CourseContentBox = styled.div`
    width: 100%;
    padding: 30px;
`;
const CourseSideMenu = styled.div``;

const Button = styled.button`
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 5px;
    margin-right: 10px;

    &:hover {
        background-color: #0056b3;
    }
`;

const PdfLink = styled.a`
    background-color: #28a745;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    text-decoration: none;
    cursor: pointer;

    &:hover {
        background-color: #218838;
    }
`;

const Modal = styled.div`
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
`;

const ModalContent = styled.div`
  background-color: #fff;
  padding: 30px;
  border-radius: 10px;
  width: 70%;
  max-width: 800px;
  position: relative;
  background-color: rgba(15, 16, 21, 0.5);
`;

const CloseButton = styled.button`
  position: absolute;
  top: 0px;
  right: 0px;
  font-size: 30px;
  cursor: pointer;
  background: none;
  border: none;
  color: #fff;
  padding: 0;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
`;
const ContentTitle = styled.p`
    font-size: 24px;
    font-weight: 600;
    margin: 20px 0;
    line-height: 1.4;
    color: #ffce48;
    background-color: transparent;
    border-bottom: 1px outset #fff;
`;

const ContentText = styled.p`
    font-size: 15px;
    color: #fff;
    background-color: transparent;
    line-height: 2;
`;

const LectureImg = styled.img`
    width: 100%;
    border-radius: 12px;
`;

//   
const Table = styled.table`
    width: 100%;
`;

const TableHead = styled.thead`
    color: white;
`;

const TableRow = styled.tr`
    border-bottom: 1px solid #ddd;

    /* &:nth-child(even) {
    background-color: #f9f9f9;
  }

  &:hover {
    background-color: #f1f1f1;
  } */
`;

const TableHeader = styled.th`
    padding: 12px;
    background-color: #556b2f;
    text-align: center;
`;

const TableData = styled.td`
    padding: 12px;
    color: #fff;
`;

const CourseOption = styled.div`
    background-color: #1c1f27;
    margin-bottom: 20px;
    border-radius: 12px;
    width: 100%;
    padding: 20px;
    position: relative;
`;

const LectureName = styled.p`
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 12px;
    line-height: 1.4;
    color: #ffce48;
    background-color: transparent;
`;

const LectureContentText = styled.p`
    font-size: 15px;
    color: #fff;
    background-color: transparent;
    line-height: 2;
`;

const formatPlaytime = (playtime) => {
    if (playtime.length !== 6) return "00:00:00";
    const hours = playtime.substring(0, 2);
    const minutes = playtime.substring(2, 4);
    const seconds = playtime.substring(4, 6);
    return `${hours}:${minutes}:${seconds}`;
};

export function Course() {
    const { userId, lectureId } = useParams();
    const [learning, setlearing] = useState([]);
    const [teacher, setTeacher] = useState([]);
    const [lecture, setLecture] = useState([]);

    const [progress, setProgress] = useState([]);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [selectedVideoPath, setSelectedVideoPath] = useState("");
    const [remainingDays, setRemainingDays] = useState("");

    useEffect(() => {
        Axios();
    }, [lectureId]);

    async function Axios() {
        // learning Content 
        try {
            const learningData = await getContentsByLectureId(lectureId);
            setlearing(learningData);
        } catch (error) {
            console.log("leaning Error", error);
        }

        // Teacher 
        try {
            const teacherData = await getTeacherByLectureId(lectureId);
            setTeacher(teacherData);
        } catch (error) {
            console.log("Teacher Error", error);
        }

        // Lecture 
        try {
            const lectureData = await getLecture(lectureId);
            setLecture(lectureData);
        } catch (error) {
            console.log("Lecture Error", error);
        }

        try {
            const progressData = await getLectureProgressDetails(
                userId,
                lectureId
            );
            setProgress(progressData);
        } catch (error) {
            console.log("Progress Error", error);
        }
    }
    //  path
    const openModal = (videoPath) => {
        setSelectedVideoPath(videoPath);
        // window.alert("openModal videoPath: " + videoPath);
        setIsModalOpen(true);
    };
    const closeModal = () => {
        setIsModalOpen(false);
        setSelectedVideoPath("");
    };
    useEffect(() => {
        const calculateRemainingDays = () => {
            const currentDate = new Date();
            const endDate = new Date(lecture.applicationPeriodEndDate);

            //    (: )
            const timeDifference = endDate - currentDate;
            const dayDifference = Math.ceil(
                timeDifference / (1000 * 3600 * 24)
            );

            //   
            setRemainingDays(dayDifference);
        };

        calculateRemainingDays();
    }, [lecture.applicationPeriodEndDate]);

    console.log(learning);
    console.log("2", progress);

    const maxLength = Math.max(learning.length, progress.length);
    return (
        <>
            <Navbar />
            <LeftSidebar />
            <Container>
                <CourseContentBox>
                    <ContentTitle>{lecture.lectureName}</ContentTitle>
                    <ContentText>{lecture.educationOverview}</ContentText>
                    <ContentTitle></ContentTitle>
                    <ContentText>{lecture.textbookInformation}</ContentText>
                    <p></p>
                    <ContentTitle></ContentTitle>
                    {teacher.map((data, index) => (
                        <ContentText key={index}>
                            {data.teacherResume}
                        </ContentText>
                    ))}

                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableHeader></TableHeader>
                                <TableHeader></TableHeader>
                                <TableHeader> </TableHeader>
                                <TableHeader>PDF </TableHeader>
                                <TableHeader> </TableHeader>
                                {/* <TableHeader></TableHeader> */}
                                {/* <TableHeader> </TableHeader>
                <TableHeader>  </TableHeader> */}
                                <TableHeader> </TableHeader>
                                <TableHeader> </TableHeader>
                                {/* <TableHeader> </TableHeader> */}
                            </TableRow>
                        </TableHead>
                        <tbody>
                            {Array.from({ length: maxLength }, (_, index) => {
                                const learningData = learning[index] || {};
                                const progressData = progress[index] || {};

                                return (
                                    <TableRow key={index}>
                                        <TableData>
                                            {learningData.learningContentsSeq}
                                            
                                        </TableData>
                                        <TableData>
                                            {learningData.learningContents}
                                        </TableData>
                                        <TableData>
                                            <Button
                                                onClick={() => {
                                                    openModal(
                                                        learningData.learningVideo_path
                                                    );
                                                    // window.alert(
                                                    //     "Button Click videoPath: " +
                                                    //         learningData.learningVideo_path
                                                    // );
                                                }}
                                            >
                                                 
                                            </Button>
                                        </TableData>
                                        <TableData>
                                            <PdfLink
                                                href={
                                                    learningData.learningPdf_path
                                                }
                                                target="_blank"
                                            >
                                                PDF 
                                            </PdfLink>
                                        </TableData>
                                        <TableData>
                                            {formatPlaytime(
                                                learningData.learningPlaytime
                                            )}
                                        </TableData>
                                        {/* <TableData>
                      {learningData.completeLearningDatetime || ""}
                    </TableData> */}
                                        {/* <TableData>
                      {progressData.complete_learning_datetime}
                    </TableData>
                    <TableData>{progressData.last_learning_datetime}</TableData> */}
                                        <TableData>
                                            {progressData.learning_count}
                                        </TableData>
                                        <TableData>
                                            {progressData.learning_time}
                                            <br />
                                            {progressData.learning_playtime}
                                        </TableData>
                                    </TableRow>
                                );
                            })}
                        </tbody>
                    </Table>
                    {isModalOpen && (
                        <Modal>
                            <CloseButton onClick={closeModal}>&times;</CloseButton>
                            <ModalContent>
                                <video controls style={{ width: "100%" }}>
                                    <source
                                        src={selectedVideoPath}
                                        type="video/mp4"
                                    />
                                </video>
                            </ModalContent>
                        </Modal>
                    )}
                </CourseContentBox>
                <CourseSideMenu>
                    <LectureImg src={lecture.imagePath} />
                    <CourseOption>
                        <LectureName>{lecture.lectureName}</LectureName>
                        <LectureContentText>
                             :
                            {lecture.educationPrice === 0
                                ? ""
                                : `${lecture.educationPrice}`}
                        </LectureContentText>
                        <LectureContentText>
                             : {lecture.educationPeriodStartDate} ~{" "}
                            {lecture.educationPeriodEndDate}
                            <LectureContentText>
                                  : {remainingDays}
                            </LectureContentText>
                        </LectureContentText>
                    </CourseOption>
                </CourseSideMenu>
            </Container>
        </>
    );
}
