import React, { useState, useEffect, useRef } from "react";
import "../../../Styles/Chatroom.css";

import styled, { keyframes } from "styled-components";
import { LeftSidebar } from "../Sidebar";
import { Navbar } from "../Navbar";

import axios from "axios";

// API 
const urlCurrent = "/user/current"; //  

const fadeIn = keyframes`
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
`;

// const Container = styled.div`
//     box-sizing: border-box;
//     padding: 176px 50px 0 240px;
//     transition: all 0.3s;
//     display: grid;
//     grid-template-columns: 70% 30%;
//     animation: ${fadeIn} 0.6s ease-out;
//     width: 100%;
// `;

const Container = styled.div`
    box-sizing: border-box;
    padding: 100px 50px 0 240px;
    transition: all 0.3s;
    /* display: grid;
    grid-template-columns: 70% 30%; */
    animation: ${fadeIn} 0.6s ease-out;
    width: 100%;
`;

export function Chatroom() {
    // [ ]
    // messages:   
    // input:  
    // userId:   ID
    const [messages, setMessages] = useState([]);
    const [input, setInput] = useState("");
    const [userId, setUserId] = useState(null); // User ID 

    // [ ]
    // messagesEndRef:      
    // socketRef: WebSocket  
    const messagesEndRef = useRef(null);
    const socketRef = useRef(null); // WebSocket  ref

    // fetchUserData  API   ID   
    const fetchUserData = async () => {
        try {
            const { data } = await axios.get(urlCurrent, {
                withCredentials: true,
            }); //  User ID 
            setUserId(data.userId);
        } catch (error) {
            console.error("Error fetching user data:", error);
        }
    };

    // WebSocket  :
    //   WebSocket  ,    messages  .
    //    WebSocket  .
    useEffect(() => {
        fetchUserData(); //      

        // WebSocket  
        // socketRef.current = new WebSocket("ws://localhost:8080");
        // socketRef.current = new WebSocket("ws://localhost:3000");
        socketRef.current = new WebSocket("ws://localhost:3001");

        socketRef.current.onmessage = (event) => {
            const newMessage = JSON.parse(event.data);
            setMessages((prevMessages) => [...prevMessages, newMessage]);
        };

        return () => {
            socketRef.current.close();
        };
    }, []);

    //  :
    //    handleSubmit  
    //     ID  , WebSocket   
    // WebSocket      
    const handleSubmit = (event) => {
        event.preventDefault();
        if (input.trim() !== "" && userId !== null) {
            const message = { text: input, userId: userId }; //   

            if (
                socketRef.current &&
                socketRef.current.readyState === WebSocket.OPEN
            ) {
                socketRef.current.send(JSON.stringify(message));
                setInput("");
            } else {
                window.alert(
                    "WebSocket connection is not open. Message not sent."
                );
                console.warn(
                    "WebSocket connection is not open. Message not sent."
                );
            }
        }
    };

    //   :
    //    ,      
    useEffect(() => {
        messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
    }, [messages]);

    return (
        <>
            <Navbar />
            <LeftSidebar />
            <Container>
                <div className="chat-room">
                    <div className="messages">
                        {messages.map((msg, index) => (
                            <div key={index} className="message">
                                [{msg.userId || "Unknown"}] {msg.text}{" "}
                                {/*  ID  "Unknown"  */}
                            </div>
                        ))}
                        <div ref={messagesEndRef} />
                    </div>
                    <form onSubmit={handleSubmit} className="input-form">
                        <input
                            type="text"
                            value={input}
                            onChange={(e) => setInput(e.target.value)}
                            placeholder="Type a message"
                        />
                        <button type="submit">Send</button>
                    </form>
                </div>
            </Container>
        </>
    );
}

export default Chatroom;
