import React from "react";
import { Route, Routes, Link, Outlet, useLocation } from "react-router-dom";
import styled from "styled-components";

//    (Notices, QA, Events)
import { Notices } from "./Notices";
import { QA } from "./QA";
import { Events } from "./Events";

//  
const NavBox = styled.nav`
  width: 200px;

  /* padding: 20px 0; */
  border-radius: 5px; /*   */
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); /*   */
  margin-right: 30px;
`;

const NavBoxIn = styled.div`
  display: flex;
  flex-direction: row; /*    */
  justify-content: space-between; /*     */
  width: 100%; /*      */
  flex-wrap: nowrap; /*   */
  overflow: hidden; /*    */
  margin-left: 25px;
`;

const NavList = styled.ul`
  list-style: none;
  padding: 0;
  margin: 0;
`;

const NavItem = styled.li`
  margin-bottom: 0; /*   , margin-bottom  */

  a {
    display: block;
    padding: 12px 20px;
    text-decoration: none;
    color: ${(props) => (props.active ? "#9da2b9" : "#9da2b9")};
    background-color: ${(props) => (props.active ? "#007bff" : "transparent")};
    font-weight: ${(props) => (props.active ? "bold" : "normal")};
    border-radius: 5px;
    text-align: center;

    &:hover {
      background-color: ${(props) => (props.active ? "#0056b3" : "#23262d")};
      color: #ffffff;
    }
  }
`;

export function CommunitySidebar() {
  const location = useLocation(); //    

  //    
  const currentPath = location.pathname.split("/").pop(); //    

  return (
    <div style={{ display: "flex" }}>
      {/*   */}
      <NavBox>
        <NavBoxIn>
          <NavList>
            <NavItem className={currentPath === "notices" ? "active" : ""}>
              <Link to="/community/notices"></Link>
            </NavItem>
            <NavItem className={currentPath === "qa" ? "active" : ""}>
              <Link to="/community/qa"></Link>
            </NavItem>
            <NavItem className={currentPath === "events" ? "active" : ""}>
              <Link to="/community/events"></Link>
            </NavItem>
          </NavList>
        </NavBoxIn>
      </NavBox>

      {/*   Routes  */}
      {/* <div
        style={{
          flex: 1,
          padding: "20px",
          backgroundColor: "#0f1015",
          color: "#e0e0e0",
        }}
      > */}
      <Routes>
        <Route path="/notices" element={<Notices />} />
        <Route path="/qa" element={<QA />} />
        <Route path="/events" element={<Events />} />
      </Routes>

      {/* Outlet       */}
      <Outlet />
    </div>
    // </div>
  );
}

export default CommunitySidebar;
