export function Cart() {
    const [cart, setCart] = useState([]);
    const [checkedItems, setCheckedItems] = useState([]);
    const [userId, setUserId] = useState("");
    const [cartData, setCartData] = useState(false);
    const navigate = useNavigate();

    useEffect(() => {
        cartList();
    }, []);

    // 
    async function courseAdd() {
        if (checkedItems.length === 0) {
            alert("  .");
            return;
        }

        const confirm = window.confirm(
            ` ${checkedItems.length}   ?`
        );
        if (!confirm) {
            return;
        }

        try {
            for (const lectureId of checkedItems) {
                const courseRegistration = {
                    user: {
                        userId: userId,
                    },
                    lecture: {
                        lectureId: lectureId,
                    },
                };
                await saveCourseRegistration(courseRegistration);

                let updatedCart = cart.filter(
                    (item) => !checkedItems.includes(item.lectureId)
                );

                // localStorage 
                localStorage.setItem(userId, JSON.stringify(updatedCart));
                setCart(updatedCart);
                setCheckedItems([]);
            }
            alert("   .");
        } catch (error) {
            console.log("Course Error", error);
        }
    }

    //  
    async function cartList() {
        try {
            const SessionData = await getCurrentUser();
            const userId = SessionData.userId;
            setUserId(userId);
            const getCartList = localStorage.getItem(userId);
            const cartList = getCartList ? JSON.parse(getCartList) : [];
            setCart(Array.isArray(cartList) ? cartList : []);

            if (cartList.length === 0) {
                setCartData(true);
            } else {
                setCartData(false);
            }
        } catch (error) {
            console.error("Cart List Error:", error);
        }
    }

    //     
    const handleSelectAll = () => {
        if (cart.length === 0) return; //    

        const allItemIds = cart.map((item) => item.lectureId);
        if (checkedItems.length === cart.length) {
            setCheckedItems([]); //         
        } else {
            setCheckedItems(allItemIds); //   
        }
    };

    const handleCheckboxChange = (lectureId) => {
        setCheckedItems((prevCheckedItems) => {
            if (prevCheckedItems.includes(lectureId)) {
                return prevCheckedItems.filter((id) => id !== lectureId);
            } else {
                return [...prevCheckedItems, lectureId];
            }
        });
    };

    //   
    async function cartRemove() {
        if (checkedItems.length === 0) {
            alert("  .");
            return;
        }

        //     
        const confirm = window.confirm(
            ` ${checkedItems.length}   ?`
        );
        if (!confirm) {
            return;
        }

        try {
            let cartList = JSON.parse(localStorage.getItem(userId)) || [];

            cartList = cartList.filter(
                (item) => !checkedItems.includes(item.lectureId)
            );

            localStorage.setItem(userId, JSON.stringify(cartList));
            setCart(cartList);
            setCheckedItems([]);
        } catch (error) {
            console.log("Remove Error", error);
        }
    }

    return (
        <>
            <Navbar />
            <LeftSidebar />
            <RightSidebar />
            <Container>
                <CartTitle></CartTitle>

                <div>
                    {cart.length > 0 ? (
                        <>
                            <ChkAll>
                                <ChkAllBtn onClick={handleSelectAll}>
                                    {checkedItems.length === cart.length
                                        ? " "
                                        : " "}
                                </ChkAllBtn>
                            </ChkAll>
                            {cart.map((lecture, index) => (
                                <CartForm key={index}>
                                    <Chk
                                        type="checkbox"
                                        checked={checkedItems.includes(
                                            lecture.lectureId
                                        )}
                                        onChange={() =>
                                            handleCheckboxChange(
                                                lecture.lectureId
                                            )
                                        }
                                    />
                                    <CartContent>
                                        <CartImgBox>
                                            <LectureImg
                                                src={lecture.imagePath}
                                            />
                                        </CartImgBox>
                                        <CartTextBox>
                                            <CartItem>
                                                 : {lecture.lectureName}
                                            </CartItem>
                                            <CartItem>
                                                 : {lecture.educationPrice}
                                            </CartItem>
                                            <CartItem>
                                                 :{" "}
                                                {lecture.category.categoryName}
                                            </CartItem>
                                        </CartTextBox>
                                    </CartContent>
                                </CartForm>
                            ))}
                            <RemoveBtnBox>
                                <RemoveBtn onClick={cartRemove}>
                                    
                                </RemoveBtn>
                                <CourseBtn onClick={courseAdd}>
                                    
                                </CourseBtn>
                            </RemoveBtnBox>
                        </>
                    ) : (
                        <CartNoneBox>
                            <CartNone>
                                <CartNoneImg src="/reactimage/Cart.png" />
                            </CartNone>
                            <CartNoneText> </CartNoneText>
                            <LectureMoveBox>
                                <LectureMoveBtn
                                    onClick={() => navigate("/lecture")}
                                >
                                    
                                </LectureMoveBtn>
                            </LectureMoveBox>
                        </CartNoneBox>
                    )}
                </div>
            </Container>
        </>
    );
}
