import React, { useState, useEffect } from "react";
import axios from "axios";
import "../../../Styles/MyPageUserDelete.css";
import styled, { keyframes } from "styled-components";
import { useNavigate } from "react-router-dom";

const fadeIn = keyframes`
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
`;

const AnimationBox = styled.div`
    padding: 10px;
    animation: ${fadeIn} 0.6s ease-out;
`;

const UserDeleteBox = styled.div`
    width: 100%;
`;

const HeaderText = styled.p`
    color: #556b2f;
    font-size: 30px;
    font-weight: 700;
`;

const Container = styled.div`
    padding: 50px;
    width: 100%;
`;

const TextContainer = styled.div`
    width: 100%;
    border: 1px solid #556b2f;
`;

const TextBox = styled.div`
    width: 60%;
    padding: 20px;
    text-align: left;
    margin: 0 auto;
`;

const TextTitle = styled.p`
    font-size: 28px;
    color: #556b2f;
    font-weight: 800;
    margin-bottom: 10px;
`;

const DeleteText = styled.p`
    color: #fff;
    font-size: 14px;
    line-height: 50px;
`;

const CheckBox = styled.div`
    width: 100%;
    margin-top: 50px;
    text-align: center;
`;

const ChkText = styled.p`
    margin-left: 20px;
    color: #fff;
    display: inline-block;
`;

const InputfadeIn = keyframes`
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
`;

const TextAreaBox = styled.div`
    animation: ${InputfadeIn} 0.6s ease-out;
`;

export function MyPageUserDelete() {
    const [userId, setUserId] = useState("");
    const [userOffice, setUserOffice] = useState("DW ");
    const [joinDate, setJoinDate] = useState("");
    const [userDeleteReason, setUserDeleteReason] = useState("");
    const [textMinLengthAlert, setTextMinLengthAlert] = useState(false);
    const [showTextArea, setShowTextArea] = useState(false); //     
    const navigate = useNavigate();

    const urlCurrent = "/user/current";
    const urlDelete = "/api/withdrawal/saveWithdrawal";
    const urlLogout = "/user/logout";

    useEffect(() => {
        axios
            .get(urlCurrent, { withCredentials: true })
            .then((response) => {
                setUserId(response.data.userId);
                setJoinDate(response.data.sysDate.substring(0, 10));
            })
            .catch((error) => {
                console.error(" : ", error);
            });
    }, []);

    const handleDeleteReasonChange = (e) => {
        const reason = e.target.value;
        setUserDeleteReason(reason);

        if (reason.length <= 1) {
            setTextMinLengthAlert(true);
        } else {
            setTextMinLengthAlert(false);
        }
    };

    const handleDeleteClick = () => {
        const userDeleteCheck = document.querySelector(".deleteSelectBox");

        if (userDeleteCheck.checked) {
            const today = new Date();
            const year = today.getFullYear();
            const month = ("0" + (today.getMonth() + 1)).slice(-2);
            const day = ("0" + today.getDate()).slice(-2);

            const deleteData = {
                withdrawalId: 1,
                sysDate: today,
                updDate: today,
                withdrawalDate: `${year}-${month}-${day}`,
                user: {
                    userId: userId,
                },
                withdrawalReason: userDeleteReason,
            };

            if (window.confirm(" ?")) {
                axios
                    .post(urlDelete, deleteData, { withCredentials: true })
                    .then((response) => {
                        alert(" .");
                        axios
                            .post(urlLogout, {}, { withCredentials: true })
                            .then((response) => {
                                if (response.status === 200) {
                                    navigate("/login");
                                }
                            })
                            .catch((error) => {
                                console.error("  : ", error);
                            });
                    })
                    .catch((error) => {
                        console.error("  : ", error);
                    });
            }
        } else {
            alert("  .");
        }
    };

    const handleResizeHeight = (e) => {
        const textarea = e.target;
        textarea.style.height = "auto";
        textarea.style.height = `${textarea.scrollHeight}px`;
    };

    const handleCheckboxChange = (e) => {
        setShowTextArea(e.target.checked); //      
    };

    return (
        <AnimationBox>
            <UserDeleteBox>
                <HeaderText></HeaderText>
                <Container>
                    <TextContainer>
                        <TextBox>
                            <TextTitle>
                                   ,   
                                .
                            </TextTitle>
                            <DeleteText>
                                        
                                     .
                            </DeleteText>
                            <DeleteText>
                                        .
                            </DeleteText>
                            <DeleteText>
                                      LMS 
                                , LMS  .
                            </DeleteText>

                            <CheckBox>
                                <input
                                    className="deleteSelectBox"
                                    type="checkbox"
                                    onChange={handleCheckboxChange} //     
                                    style={{ width: "10px", height: "10px" }}
                                />
                                <ChkText>  .</ChkText>
                            </CheckBox>

                            {/*       */}
                            {showTextArea && (
                                <TextAreaBox
                                    style={{
                                        marginTop: "20px",
                                        backgroundColor: "#fff",
                                    }}
                                >
                                    <textarea
                                        id="userDeleteReason"
                                        rows="3"
                                        placeholder="  "
                                        onChange={handleDeleteReasonChange}
                                        onInput={handleResizeHeight}
                                        style={{
                                            fontSize: "14px",
                                            width: "80%",
                                        }}
                                    ></textarea>
                                </TextAreaBox>
                            )}

                            {textMinLengthAlert && (
                                <div className="textMinLengthAlert">
                                    *  2  .
                                </div>
                            )}

                            <div
                                className="userDeleteBtn"
                                onClick={handleDeleteClick}
                            >
                                
                            </div>
                        </TextBox>
                    </TextContainer>
                </Container>
            </UserDeleteBox>
        </AnimationBox>
    );
}

export default MyPageUserDelete;
